// import APIHeader from "../configs/APIHeader";
import APIPaths from "../configs/APIPaths";
// import APIResult from "../configs/APIResult";

import { debugPrint } from "./Utilities";

const callAPI = async ({ method = "GET", url, headers = {}, body }) => {
  const requestOptions = {
    method,
    headers,
  };

  headers["apikey"] = `j3j389#nklala2`;

  if (body) {
    if (typeof body === "string") {
      requestOptions.body = body;
      headers["Content-Type"] = "text/plain";
    } else if (body instanceof FormData) {
      requestOptions.body = body;
    } else {
      requestOptions.body = JSON.stringify(body);
      headers["Content-Type"] = "application/json";
    }
  } else {
    headers["Content-Type"] = "application/json";
  }

  try {
    // debugPrint(
    //   `%c${method} ${url}`,
    //   "color: red; font-size: 1rem",
    //   "\nheaders:",
    //   headers,
    //   "\nbody:",
    //   body
    // );
    const result = await fetch(url, requestOptions);

    // Check if response is JSON before parsing
    const contentType = result.headers.get("content-type");
    if (contentType && contentType.includes("application/json")) {
      const responseBody = await result.json();
      // debugPrint("response:", responseBody);
      return responseBody;
    } else {
      const responseText = await result.text();
      // debugPrint("response (text):", responseText);
      try {
        const parsedJSON = JSON.parse(responseText);
        // debugPrint("Converted text to JSON:", parsedJSON);
        return parsedJSON;
      } catch (error) {
        // debugPrint("Failed to convert text to JSON:", error);
        return responseText;
      }
    }
  } catch (error) {
    // debugPrint("Fetch error:", error);
    return;
  }
};

export async function getHomepage() {
  var url = window.apiEndPoint + APIPaths.getHomepage;

  return callAPI({
    url: url,
    method: "GET",
  });
}

export async function getArticle(id) {
  var body = {
    id: id,
  };

  var url = window.apiEndPoint + APIPaths.getArticle;

  return callAPI({
    url: url,
    method: "POST",
    body: body,
  });
}


