import { useGeneralContext } from "../../context/GeneralContext";
import styles from "./ArticleDialog.module.scss";
import CloseBlueIcon from "../../assets/images/full/closeBlueIcon.png";
import { Button } from "@mui/material";
import { useEffect, useState } from "react";
import DOMPurify from "dompurify";

export default function ArticleDialog() {
  const [isDialogVisible, setIsDialogVisible] = useState(false);
  const [isClosing, setIsClosing] = useState(false); // New state to track closing animation

  const { handleArticleDialogOpen, articleData, setArticleData } =
    useGeneralContext();

  useEffect(() => {
    if (articleData && Object.keys(articleData).length > 0) {
      setIsDialogVisible(true); // Open when data is loaded
      setIsClosing(false); // Reset closing state
    }
  }, [articleData]);

  useEffect(() => {
    if (isDialogVisible) {
      document.body.style.overflow = "hidden"; // Prevent scrolling
      document.body.style.touchAction = "none"; // Block touch gestures
    } else {
      document.body.style.overflow = ""; // Restore scrolling
      document.body.style.touchAction = ""; // Restore touch gestures
    }
  }, [isDialogVisible]);

  // Handle closing with delay
  const closeDialog = () => {
    setIsClosing(true); // Start closing animation

    setTimeout(() => {
      setIsDialogVisible(false); // Hide after animation completes
      setIsClosing(false); // Reset closing state
      handleArticleDialogOpen();
      setArticleData({});
    }, 300); // Match CSS transition duration (0.3s)
  };

  return (
    <div
      className={`${styles.articleDialogWrapper} ${
        isDialogVisible ? styles.showArticleDialog : ""
      } ${isClosing ? styles.closing : ""}`}
    >
      <div className={styles.articleDialogContainer}>
        <div className={styles.closeButtonWrapper}>
          <Button
            variant="text"
            onClick={closeDialog}
            style={{ minWidth: "min-content", padding: "0px" }}
          >
            <img
              src={CloseBlueIcon}
              alt="CloseIcon"
              className={styles.closeIcon}
            />
          </Button>
        </div>

        <div style={{ overflow: "hidden auto", width: "100%" }}>
          <div className={styles.title} data-aos="fade-down">
            <h1>{articleData.title || "-"}</h1>
          </div>
          <div className={styles.content} data-aos="fade-up">
            {/* {articleData.cover && (
              <div className={styles.imgWrapper}>
                <img
                  src={articleData.cover}
                  alt="article-img"
                  style={{
                    width: "100%",
                    maxHeight: "300px",
                    objectFit: "contain",
                  }}
                />
              </div>
            )} */}

            <div
              className={styles.descriptionWrapper}
              dangerouslySetInnerHTML={{
                __html:
                  DOMPurify.sanitize(articleData.content, {
                    ADD_TAGS: ["iframe"],
                    ADD_ATTR: [
                      "allow",
                      "allowfullscreen",
                      "frameborder",
                      "referrerpolicy",
                    ],
                  }) || "<p>No Content</p>",
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
