import { Stack, TextField, Typography } from "@mui/material";
import styles from "./TextFieldRow.module.scss";

export default function TextFieldRow({
  label,
  placeholder,
  value,
  setValue,
  captcha = "",
  hideDisplay = false,
}) {
  const handleChange = (e) => {
    
    setValue((prevState) => {
      const key = Object.keys(prevState).find((k) => prevState[k] === value);
      if (!key) return prevState;
      return {
        ...prevState,
        [key]: e.target.value,
      };
    });
     //console.log(value);
  };
  return (
    <Stack
      direction="row"
      spacing={2}
      justifyContent="center"
      alignItems="center"
      width="100%"
      className={styles.TextFieldRowWrapper}
      sx={{
        display: hideDisplay ? "none" : "flex",
      }}
    >
      <Typography
        className={styles.title}
        sx={{
          fontFamily: "RajdhaniBold",
          width: captcha.length === 0 ? 200 : "unset",
          textAlign: "left",
          textWrap: "nowrap",
        }}
        data-aos="fade-right"
      >
        {label}
      </Typography>
      {captcha!="" && (
        <img style={{maxWidth:"40%"}}src={captcha} alt="Captcha" className={styles.captcha} />
      )}
      <TextField
        variant="outlined"
        className={styles.textField}
        value={value}
        placeholder={placeholder}
        onChange={handleChange}
        data-aos="fade-left"
        sx={(theme) => ({
          width: "100%",
          background: "#FFFFFF",
          border: "none",
          borderRadius: "4px",
          "& input": {
            userSelect: "none",
            pointerEvents: "none",
          },
          "& .MuiOutlinedInput-root": {
            "& fieldset": {
              border: "none", // Ensures a clean border
            },
            "&:hover fieldset": {
              border: "1px solid #999", // Slightly darker border on hover
            },
            "&.Mui-focused fieldset": {
              border: "2px solid #6DC19D", // Custom focus border color
            },
          },
          "& .MuiOutlinedInput-input": {
            fontSize: "18px",
            padding: "10px 16px",
            [theme.breakpoints.down("md")]: {
              fontSize: "16px",
              padding: "8px 12px",
            },
            [theme.breakpoints.down("sm")]: {
              fontSize: "14px",
              padding: "6px 10px",
            },
          },
        })}
      />
    </Stack>
  );
}
