import { Button, MenuItem, Select, Stack, Typography } from "@mui/material";
import styles from "./ReportCheck.module.scss";
import TextFieldRow from "../TextFieldRow/TextFieldRow";
import CounterCardItem from "../CounterCardItem/CounterCardItem";
import {
  useLanguageContext,
  useTranslation,
} from "../../context/LanguageContext";
import { languageOptions } from "../../utilities/Utilities";
import { useGeneralContext } from "../../context/GeneralContext";
import React, { forwardRef, useImperativeHandle } from "react";

const buttonStyle = {
  fontFamily: "RajdhaniRegular",
  letterSpacing: 1.5,
  color: "#FFFFFF",
  paddingTop: "10px",
};

export const dividerStyle = {
  color: "#FFFFFF",
  fontFamily: "RajdhaniRegular",
  userSelect: "none",
  paddingLeft: "8px",
};

//export default function ReportCheck({
const ReportCheck = forwardRef(({
  inputData,
  setInputData,
  countJumlahPelawat,
  countPelawatHariIni,
  countCarian,
  checkReport,
  captchaImg
}, 
ref) => {
  const { language, changeLanguage } = useLanguageContext();
  const { handleReportCheckDialogOpen, screenWidth } = useGeneralContext();

  const checkClicked = () => {
    checkReport();
    //handleReportCheckDialogOpen();
  };

  useImperativeHandle(ref, () => ({
    openReportDialog: handleReportCheckDialogOpen,
  }));
  
  const t = useTranslation();

  // useEffect(() => {
  //   console.log(language, translation);
  //   console.log(t("ReportCheck.accountReportCheck"));
  // }, [language]);

  const renderLabel = (label) => {
    switch(label) {
      case "bank":
        return t("ReportCheck.bankAccountNo") || "No Akaun Bank";
      case "telefon":
        return t("ReportCheck.telNo") || "No Telefon";
      case "namasyarikat":
        return t("ReportCheck.companyName") || "Name Syarikat Penipuan";
      default:
        return t("ReportCheck.bankAccountNo") || "No Akaun Bank";
    }
  }
  const renderPlaceholder = (placeholder) => {
    switch(placeholder) {
      case "bank":
        return t("ReportCheck.bankAccountNoPlaceholder") || "Masukkan No Akaun Bank";
      case "telefon":
        return t("ReportCheck.telNoPlaceholder") || "Masukkan No Telefon";
      case "namasyarikat":
        return t("ReportCheck.companyNamePlaceholder") || "Masukkan Nama Syarikat Penipuan";
      default:
        return t("ReportCheck.bankAccountNoPlaceholder") || "Masukkan No Akaun Bank";
    }
  }

  const renderValue = (value) => {
    switch(value) {
      case "bank":
        return inputData.bankAccount;
      case "telefon":
        return inputData.telNo;
      case "namasyarikat":
        return inputData.companyName;
      default:
        return inputData.bankAccount;
    }
  }
  return (
    <div className={styles.reportCheckWrapper} style={{ marginTop: "-1px" }}>
      <video
        className={styles.reportCheckVideo}
        autoPlay
        loop
        playsInline
        muted
        controls={false}
        poster="https://mule.the-oaks.my/smvideobg.jpg"
      >
        <source
          src={
            screenWidth < 768
              ? "https://mule.the-oaks.my/reportCheckMobileBg.mp4"
              : "https://mule.the-oaks.my/reportCheckBg.mp4"
          }
          type="video/mp4"
        />
      </video>

      <div className={styles.content} data-aos="fade-up">
        <Stack direction={"row"} alignItems={"center"} data-aos="fade-right">
          {languageOptions.map((option, index) => (
            <Stack
              direction="row"
              alignItems="center"
              key={option.value}
              gap={1}
              pl={1}
            >
              <Button
                variant="text"
                sx={{
                  ...buttonStyle,
                  textTransform:
                    option.value === language ? "uppercase" : "none",
                  fontWeight: option.value === language ? "bold" : "normal",
                  fontFamily:
                    option.value === language
                      ? "RajdhaniBold"
                      : "RajdhaniRegular",
                  transform:
                    option.value === language
                      ? "scale(1.1) translateY(-1px)"
                      : "",
                  transition: "all 0.1s ease-in-out",
                  "&:hover": {
                    transform: "scale(1.05)", // Slightly enlarges the button
                  },
                }}
                className={styles.languageButton}
                onClick={() => changeLanguage(option.value)}
              >
                {option.label}
              </Button>
              {index < languageOptions.length - 1 && (
                <span style={dividerStyle} className={styles.languageDivider}>
                  |
                </span>
              )}
            </Stack>
          ))}
        </Stack>
        <Stack
          justifyContent={"center"}
          bgcolor={"#6DC19D"}
          fontFamily={"RajdhaniBold"}
          className={styles.reportCheckTitle}
          data-aos="fade-right"
          height={"min-content"}
        >
          <span>
            {t("ReportCheck.accountReportCheck") ||
              "Semak Akaun yang ada Report"}
          </span>
        </Stack>
        <Stack
          direction={"column"}
          justifyContent={"center"}
          alignItems={"center"}
          className={styles.reportCheckForm}
        >
          {/* Category */}
          <Stack
            direction="row"
            spacing={2}
            justifyContent="center"
            alignItems="center"
            width="100%"
            className={styles.TextFieldRowWrapper}
          >
            <Typography
              className={styles.title}
              sx={{
                fontFamily: "RajdhaniBold",
                width: 200,
                textAlign: "left",
                textWrap: "nowrap",
              }}
              data-aos="fade-right"
            >
              {t("ReportCheck.category") || "Kategori"}
            </Typography>
            <Select
              variant="outlined"
              className={styles.textField}
              value={inputData.category}
              onChange={(e) => {
                // console.log(e.target.value);
                setInputData((prevState) => ({
                  ...prevState,
                  category: e.target.value,
                }));
              }}
              sx={(theme) => ({
                width: "100%",
                background: "#FFFFFF",
                border: "none",
                borderRadius: "4px",
                fontFamily: "PoppinsRegular",
                "& .MuiOutlinedInput-notchedOutline": {
                  borderColor: "#FFFFFF",
                },
                "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                  borderColor: "#6DC19D",
                },
                "&:hover .MuiOutlinedInput-notchedOutline": {
                  borderColor: "rgba(228, 219, 233, 0.25)",
                },
                ".MuiSvgIcon-root ": {
                  fill: "#1e1e1e !important",
                },
                "& input": {
                  userSelect: "none",
                  //pointerEvents: "none",
                },
                "& .MuiOutlinedInput-input": {
                  fontSize: "18px",
                  padding: "10px 16px",
                  [theme.breakpoints.down("md")]: {
                    fontSize: "16px",
                    padding: "8px 12px",
                  },
                  [theme.breakpoints.down("sm")]: {
                    fontSize: "14px",
                    padding: "6px 10px",
                  },
                },
              })}
            >
              <MenuItem disabled value="">
                <em>{t("ReportCheck.categoryPlaceholder")}</em>
              </MenuItem>
              <MenuItem value={"bank"}>{t("ReportCheck.bankAccountNo")}</MenuItem>
              <MenuItem value={"telefon"}>{t("ReportCheck.telNo")}</MenuItem>
              <MenuItem value={"namasyarikat"}>{t("ReportCheck.companyName")}</MenuItem>
            </Select>
          </Stack>

          {/* Bank Account or Phone */}
          <TextFieldRow
            label={
              renderLabel(inputData.category)
            }
            placeholder={
              renderPlaceholder(inputData.category)
            }
            value={
              renderValue(inputData.category)
            }
            setValue={setInputData}
          />

          {/* Captcha */}
          <TextFieldRow
            label={t("ReportCheck.captcha") || "Captcha"}
            placeholder={
              t("ReportCheck.captchaPlaceholder") || "Perkataan Dalam Imej Kiri"
            }
            value={inputData.captcha}
            setValue={setInputData}
            captcha={captchaImg}
            hideDisplay={window.disableCaptcha}
          />

          {/* Disclaimer */}
          <Stack
            justifyContent={"center"}
            bgcolor={"#DE5859"}
            fontFamily={"RajdhaniLight"}
            className={styles.disclaimerWrapper}
            data-aos="fade-zoom-in"
          >
            <Typography
              className={styles.disclaimerText}
              sx={{
                lineBreak: "auto",
                wordBreak: "break-word",
              }}
            >
              {t("ReportCheck.disclaimer") || "PENAFIAN"}
            </Typography>
            <Typography
              className={styles.disclaimerText}
              sx={{ lineBreak: "auto", wordBreak: "break-word" }}
            >
              {t("ReportCheck.disclaimerContent1") ||
                " Kerajaan Malaysia dan PDRM tidak bertanggungjawab di atas kehilangan atau kerosakan disebabkan penggunaan mana-mana maklumat yang diperolehi daripada laman web ini."}
            </Typography>
            <Typography
              className={styles.disclaimerText}
              sx={{ lineBreak: "auto", wordBreak: "break-word" }}
            >
              {t("ReportCheck.disclaimerContent2") ||
                "Ia tidak memberikan hak kepada mana-mana pihak untuk bertindak bagi PDRM dalam apa-apa jua urusan."}
              
            </Typography>
          </Stack>

          <Button
            variant="contained"
            sx={{
              color: "#6DC19D",
              backgroundColor: "#000000",
              border: "2px solid #6DC19D",
              borderRadius: "12px",
            }}
            className={styles.checkButton}
            data-aos="zoom-in-up"
            onClick={checkClicked}
          >
            {t("ReportCheck.check") || "Semak"}
          </Button>
        </Stack>

        <CounterCardItem
          countJumlahPelawat={countJumlahPelawat}
          countPelawatHariIni={countPelawatHariIni}
          countCarian={countCarian}
        />
      </div>
    </div>
  );
}
);

export default ReportCheck;
