import { debugPrint } from "../../utilities/Utilities";
import styles from "./YoutubeSection.module.scss";

export default function YoutubeSection({ youtubeEmbedData }) {
  return (
    <div className={styles.youtubeEmbedWrapper}>
      {Array.isArray(youtubeEmbedData) &&
        youtubeEmbedData.length > 0 &&
        youtubeEmbedData.map((item, index) => {
          if (!item?.url) return null;
          let extraParams;
          let videoId = null;

          try {
            const url = new URL(item.url);

            if (url.pathname.includes("/watch")) {
              videoId = url.searchParams.get("v");
              extraParams = "?loop=1&playlist=" + videoId;
            } else if (url.pathname.includes("/shorts/")) {
              extraParams = "";
              videoId = url.pathname.split("/shorts/")[1]?.split("?")[0];
            }
          } catch (error) {
            debugPrint("Invalid/Empty URL:", item.url);
            return null;
          }

          if (!videoId) return null;

          return (
            <div
              key={`${index}-${item.url}`}
              className={styles.youtubeEmbedItem}
              data-aos="zoom-in-up"
            >
              <iframe
                width="100%"
                height="315"
                src={`https://www.youtube.com/embed/${videoId}${extraParams}`}
                title={item.title || "YouTube Video"}
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                referrerPolicy="strict-origin-when-cross-origin"
                allowFullScreen
              />
            </div>
          );
        })}
    </div>
  );
}
