import { createContext, useContext, useState, useEffect } from "react";
import en from "../locales/en.json"; // Import English translations
import ms from "../locales/ms.json"; // Import Malay translations
import zh from "../locales/zh.json"; // Import Chinese translations
import ta from "../locales/ta.json"; // Import Tamil translations

// Create the Language Context
const LanguageContext = createContext();

// Available translations
const translations = { en, ms, zh, ta };

export const LanguageProvider = ({ children }) => {
  const [language, setLanguage] = useState("ms"); // Default language
  const [translation, setTranslation] = useState(translations["ms"]); // Default translation

  useEffect(() => {
    const savedLanguage = localStorage.getItem("appLanguage") || "ms";
    setLanguage(savedLanguage);
    setTranslation(translations[savedLanguage]); // Load saved language translations
  }, []);

  const changeLanguage = (lang) => {
    setLanguage(lang);
    setTranslation(translations[lang]); // Load the new translations
    localStorage.setItem("appLanguage", lang);
  };

  return (
    <LanguageContext.Provider value={{ language, translation, changeLanguage }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguageContext = () => useContext(LanguageContext);

export const useTranslation = () => {
  const { translation } = useLanguageContext();

  return (key) => {
    const keys = key.split(".");
    let value = translation;
    let fallback = ms; // Use Malay (ms) as fallback

    for (let k of keys) {
      if (value && value[k] !== undefined) {
        value = value[k]; // Navigate deeper in selected language
      } else if (fallback && fallback[k] !== undefined) {
        fallback = fallback[k]; // Navigate deeper in Malay
      } else {
        console.warn(`Missing translation for key: ${key}`);
        return key; // Return the key if both are missing
      }
    }

    return value || fallback || key;
  };
};
