import CountUp from "react-countup";
import styles from "./CounterCardItem.module.scss";
import { useTranslation } from "../../context/LanguageContext";

export default function CounterCardItem({
  countJumlahPelawat,
  countPelawatHariIni,
  countCarian,
  delay = 0,
  duration = 4,
  popUp = false,
}) {
  const t = useTranslation();

  return (
    <div className={styles.counterCardWrapper}>
      <div className={styles.counterBlock} data-aos={popUp ? "fade-right" : ""}>
        <CountUp
          start={0}
          end={countJumlahPelawat}
          delay={delay}
          duration={duration}
        >
          {({ countUpRef }) => (
            <div className={styles.counterNumber}>
              <span ref={countUpRef} />
            </div>
          )}
        </CountUp>
        <div className={styles.counterText}>
          {t("CounterCardItem.totalVisitorsCount") || "JUMLAH PELAWAT"}
        </div>
      </div>

      <div
        className={styles.counterBlock}
        style={popUp ? {} : { border: "2px solid #267173" }}
        data-aos={popUp ? "fade-zoom-in" : ""}
        data-aos-easing={popUp ? "ease-in-back" : ""}
      >
        <CountUp
          start={0}
          end={countPelawatHariIni}
          delay={delay}
          duration={duration}
        >
          {({ countUpRef }) => (
            <div className={styles.counterNumber}>
              <span ref={countUpRef} />
            </div>
          )}
        </CountUp>
        <div className={styles.counterText}>
          {t("CounterCardItem.todayVisitorsCount") || "PELAWAT HARI INI"}
        </div>
      </div>

      <div className={styles.counterBlock} data-aos={popUp ? "fade-left" : ""}>
        <CountUp start={0} end={countCarian} delay={delay} duration={duration}>
          {({ countUpRef }) => (
            <div className={styles.counterNumber}>
              <span ref={countUpRef} />
            </div>
          )}
        </CountUp>
        <div className={styles.counterText}>
          {t("CounterCardItem.searchCount") || "CARIAN"}
        </div>
      </div>
    </div>
  );
}
