import { createContext, useContext, useState, useEffect } from "react";
import { getArticle } from "../utilities/Request";

// Create the Context
const GeneralContext = createContext();

export const GeneralProvider = ({ children }) => {
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
  const [screenHeight, setScreenHeight] = useState(window.innerHeight);
  const [articleData, setArticleData] = useState({});
  const [isArticleDialogOpened, setIsArticleDialogOpened] = useState(false);
  const [isReportCheckDialogOpened, setIsReportCheckDialogOpened] =
    useState(false);
  const [scrollY, setScrollY] = useState(window.scrollY);

  const handleReportCheckDialogOpen = () => {
    //console.log("handleReportCheckDialogOpen" + isReportCheckDialogOpened);
    setIsReportCheckDialogOpened(!isReportCheckDialogOpened);
  };

  const handleArticleDialogOpen = () => {
    setIsArticleDialogOpened(!isArticleDialogOpened);
  };

  const getArticleData = async (id) => {
    const result = await getArticle(id);
    setArticleData({ ...result.article });
    console.log(result);

    return result;
  };

  useEffect(() => {
    const handleResize = () => {
      setScreenWidth(window.innerWidth);
      setScreenHeight(window.innerHeight);
      //   console.log(screenWidth, screenHeight);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [screenWidth, screenHeight]);

  useEffect(() => {
    const handleScroll = () => setScrollY(window.scrollY);
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <GeneralContext.Provider
      value={{
        screenWidth,
        screenHeight,
        isArticleDialogOpened,
        isReportCheckDialogOpened,
        articleData,
        scrollY,
        setIsArticleDialogOpened,
        handleArticleDialogOpen,
        handleReportCheckDialogOpen,
        getArticleData,
        setArticleData,
      }}
    >
      {children}
    </GeneralContext.Provider>
  );
};

export const useGeneralContext = () => {
  const context = useContext(GeneralContext);
  if (!context) {
    throw new Error("useGeneralContext must be used within a GeneralProvider");
  }
  return context;
};
