import styles from "./ReportCheckDialog.module.scss";
import CloseBlueIcon from "../../assets/images/full/closeBlueIcon.png";
// import CheckReportLoadingDialogAds from "../../assets/images/full/checkReportLoadingDialogAdsOld.png";
import CheckReportLoadingDialogAdsNew from "../../assets/images/full/checkReportLoadingDialogAdsNew.jpeg";
import ReportCheckResultDialogAds from "../../assets/images/full/reportCheckResultDialogAds.png";
import ReportCheckResultBg from "../../assets/images/full/reportCheckResultDialogBg.png";
import ReportCheckLoadingBg from "../../assets/images/full/reportCheckLoadingDialogBg.png";
import { useTranslation } from "../../context/LanguageContext";
import { useEffect, useState } from "react";
import { useGeneralContext } from "../../context/GeneralContext";
import { Button, Paper, Stack, Box } from "@mui/material";
import { BarLoader } from "react-spinners";
import { DataGrid } from "@mui/x-data-grid";





const paginationModel = { page: 0, pageSize: 5 };

export default function ReportCheckDialog({
  headers,
  rows,
  isLoadingData,
  adsImg,
  adsResultImg,
  url2,
  url3,
  resultCount = 0,
  resultCategory = "",
  resultKeyword = "",
  searchError = "",
}) {
  const { isReportCheckDialogOpened, handleReportCheckDialogOpen } =
    useGeneralContext();
  //const [isLoadingData, setIsLoadingData] = useState(true);
  const [isClosing, setIsClosing] = useState(false);
  const t = useTranslation();

  useEffect(() => {
    document.body.style.overflow = isReportCheckDialogOpened ? "hidden" : "";
    document.body.style.touchAction = isReportCheckDialogOpened ? "none" : "";
    
    if (isReportCheckDialogOpened) {
      
    }
  }, [isReportCheckDialogOpened]);

  const closeDialog = () => {
    setIsClosing(true);
    setTimeout(() => {
      handleReportCheckDialogOpen();
      setIsClosing(false);
      //setIsLoadingData(true);
    }, 300);
  };

  return (
    <div
      className={`${styles.reportCheckDialogWrapper} ${
        isReportCheckDialogOpened ? styles.showreportCheckDialog : ""
      } ${isClosing ? styles.closing : ""}`}
    >
      <div
        className={styles.reportCheckDialogContainer}
        style={
          isLoadingData
            ? { backgroundImage: `url(${ReportCheckLoadingBg})` }
            : { backgroundImage: `url(${ReportCheckResultBg})` }
        }
      >
        {isLoadingData === false ? 
        <div className={styles.closeButtonWrapper}>
          <Button
            variant="text"
            onClick={closeDialog}
            sx={{ minWidth: "min-content", padding: 0 }}
          >
            <img
              src={CloseBlueIcon}
              alt="CloseIcon"
              className={styles.closeIcon}
            />
          </Button>
        </div>
        : "" }
        {isLoadingData && (
          <p className={styles.title}><b>{t("ReportCheckDialog.title")}</b></p>
        )}

        {isLoadingData ? (
          <Stack
            alignItems="center"
            sx={{
              height: "100%",
              gap: 2,
              overflowY: "auto",
            }}
          >
            <Box sx={{ width: "100%" }}>
              <BarLoader
                height={35}
                width="100%"
                color="#FFFFFF"
                speedMultiplier={0.6}
                loading
              />
            </Box>
            <p className={styles.loadingText}>
              {t("ReportCheckDialog.loading")}
            </p>
            <div className={styles.loadingAdsWrapper}>
              <a href={url2} target="_blank" rel="noopener">
                <img
                  src={adsImg || CheckReportLoadingDialogAdsNew}
                  alt="Whoscall Semakmule"
                  className={styles.loadingAds}
                />
              </a>
            </div>
          </Stack>
        ) : (
          (searchError !== "" || resultKeyword === "" || resultKeyword == null) ?
            <Stack
              alignItems="center"
              width="100%"
              height="100%"
              gap={2}
              sx={{ overflowY: "auto" }}
            >

              <a href={url3} target="_blank" rel="noopener">
                <img
                  src={adsResultImg || ReportCheckResultDialogAds}
                  alt="Result Ad"
                  className={styles.ads}
                  style={{ width: "100%", maxWidth: "600px" }}
                />
              </a>
              <p><b>{t("ReportCheckDialog.result")}</b></p>
              <p className={styles.errorText}>{t("ReportCheck.searchError")}</p>
            </Stack>
            :
            <Stack
              alignItems="center"
              width="100%"
              height="100%"
              gap={2}
              sx={{ overflowY: "auto" }}
            >

              <a href={url3} target="_blank" rel="noopener">
                <img
                  src={adsResultImg || ReportCheckResultDialogAds}
                  alt="Result Ad"
                  className={styles.ads}
                  style={{ width: "100%", maxWidth: "600px" }}
                />
              </a>
              <p><b>{t("ReportCheckDialog.result")}</b></p>
              <p>{t("ReportCheckDialog.searchfor")}{resultCategory + ": " + resultKeyword}</p>
              {rows.length > 0 ? 
                <table className={styles.table}>
                  <thead>
                    <tr>
                      {headers.map((header, index) => (
                        <th key={index}>{header}</th>
                      ))}
                    </tr>
                  </thead>
                  <tbody>
                    {rows.map((row, index) => (
                      <tr key={index}>
                        {row.map((cell, cellIndex) => (
                          <td key={cellIndex}>{cell}</td>
                        ))}
                      </tr>
                    ))}
                  </tbody>
                </table>
              :
                <p className={styles.noResultText}>{t("ReportCheckDialog.noresult")}</p>
              }
              {resultCount > 0 && (
                <p className={styles.recordCount}>
                  {t("ReportCheckDialog.resultCount1") +
                  resultCount +
                  t("ReportCheckDialog.resultCount2")}
                </p>
              )}
            </Stack>
        )}
      </div>
    </div>
  );
}
