import React, { useEffect, useRef } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styles from "./Carousel.module.scss";
import LogoArrow from "../../assets/images/full/logoArrow.png";
import JsjkExample from "../../assets/images/full/jsjkExample.png";
import { useGeneralContext } from "../../context/GeneralContext";

const Carousel = ({ jsjkInfoData }) => {
  const carouselRef = useRef(null);
  const { screenWidth, screenHeight, handleArticleDialogOpen, getArticleData } =
    useGeneralContext();
  const sliderRef = useRef(null);
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: "150px",
    arrows: true,
    autoplay: true,
    autoplaySpeed: 3000,
    pauseOnHover: true,
    draggable: true,
    swipe: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          centerPadding: "150px",
          arrows: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          centerPadding: "0px",
          arrows: true,
        },
      },
      {
        breakpoint: 512,
        settings: {
          centerPadding: "0px",
          arrows: true,
        },
      },
    ],
  };

  useEffect(() => {
    if (sliderRef.current) {
      const prevButton = document.querySelector(".slick-prev");
      const nextButton = document.querySelector(".slick-next");

      if (prevButton && nextButton) {
        prevButton.style.left = screenWidth > 768 ? "-30px" : "-24px";
        nextButton.style.right = screenWidth > 768 ? "-30px" : "-24px";
        prevButton.style.opacity = "0";
        nextButton.style.opacity = "0";
      }
    }
  }, [screenWidth, screenHeight]);

  return (
    <div className={styles.carouselContainer} ref={carouselRef}>
      <img
        src={LogoArrow}
        alt="Previous"
        width={20}
        height={24}
        className={styles.arrowPrev}
      />
      <div data-aos="fade-up">
        <Slider ref={sliderRef} {...settings}>
          {jsjkInfoData &&
            jsjkInfoData.length > 0 &&
            jsjkInfoData.map((item) => (
              <div
                key={item.id}
                className={styles.card}
                onClick={
                  item.use_direct_url
                    ? () => window.open(item.direct_url, "_blank")
                    : () => {
                        handleArticleDialogOpen();
                        getArticleData(item.id);
                      }
                }
              >
                <div className={styles.realCard}>
                  <img
                    src={item.cover ? item.cover : JsjkExample}
                    alt={item.title}
                    className={styles.image}
                  />
                  <div className={styles.cardContent}>
                    <p>{item.title}</p>
                  </div>
                </div>
              </div>
            ))}
        </Slider>
      </div>

      <img
        src={LogoArrow}
        alt="Next"
        width={20}
        height={24}
        className={styles.arrowNext}
      />
    </div>
  );
};

export default Carousel;
